import selects from '../../common/js/xfl-select.vue'
export default {
	data() {
		return {
			list: [{
					value: "全部",
					label: 0
				},
				{
					value: "已拒绝",
					label: -1
				},
				{
					value: "已发油",
					label: 5
				},
				{
					value: "已完成",
					label: 9
				},
				{
					value: "预约已确定",
					label: 4
				},
				{
					value: "等待预约确定",
					label: 1
				}
			],
			page: 1,
			pageSize: 10,
			oil: [], //所有订单
			more: true,
			status: '',
			time: '',
			day: '',
			no: '',
			from: '',
			to: '',
		}
	},
	onLoad(option) {
		this.to = option.to;
		this.from = option.from;
		this.no = option.ordernumber;
		this.day = option.times;
		this.getReserveList(this.no);
	},
	methods: {
		getReserveList() {
			if (this.from == undefined || this.from == '' || this.from == null) {
				this.from = ''
			};
			if (this.to == undefined || this.to == '' || this.to == null) {
				this.to = ''
			};
			if (this.no == undefined || this.no == '' || this.no == null) {
				this.no = ''
			};
			this.test.post('order/search_reserve', {
				no: this.no,
				status: this.status,
				start_time: this.from,
				end_time: this.to,
				page: this.page,
				pageSize: this.pageSize,
			}).then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					// this.oil = res.data.value;
					res.data.value.forEach(el => {
						this.oil.push(el)
					})
					this.time = [];
					this.oil.forEach(el => {
						this.time.push(new Date(el.reserve_time + 8 * 3600 * 1000).toJSON().substr(0, 16).replace('T', ' ').replace(
							/-/g, '-'))
					})
					if (res.data.value.length < 10 && res.data.value.length > 0) {
						this.more = false;
					} else if (res.data.value.length == 0) {
						this.more = false;
						uni.showToast({
							title: '没有更多了',
							icon: "none",
						})
					};
				} else if (res.data.errorCode == 10001 || res.data.errorCode == 10002 || res.data.errorCode == 10003) {
					uni.showModal({
						title: '提示',
						content: res.data.message,
						success: function(res) {
							if (res.confirm) {
								uni.reLaunch({
									url: '../login/login'
								})
							} else {
								uni.reLaunch({
									url: '../login/login'
								})
							}
						}
					})
				} else {
					uni.showToast({
						title: res.data.message,
						icon: 'none'
					})
				}
			}).catch(err => {
				console.log(err)
			})
		},
		reserveList(rId, oId) {
			uni.navigateTo({
				url: 'confirmed/confirmed?reserve_id=' + rId + '&no=' + oId,
			})
		},
		complete() {
			// this.$router.push('/complete');
		},
		changeMsg(e) {
			this.oil = [];
			this.page = 1;
			this.status = e.orignItem.label;
			this.getReserveList();

		},
		Smore() {
			this.page += 1;
			this.getReserveList();
		},
		searchs() {
			uni.navigateTo({
				url: "../search/search?name=" + 'reserveOilList'
			})
		},
		back() {
			uni.switchTab({
				url: '../index/index',
			})
		},
	},
	// onNavigationBarButtonTap(e) {
	// 	
	// },
	components: {
		selects
	}
}
